import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Ocorrencia_periodicidade } from '../../../modelo/Ocorrencia_periodicidade'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'ocorrencia_periodicidade-dados-details',   templateUrl: 'ocorrencia_periodicidade_dadosDetails.component.html' }) export class Ocorrencia_periodicidadeDadosDetailsComponent implements OnInit {    ocorrencia_periodicidade: Ocorrencia_periodicidade;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.ocorrencia_periodicidade.Ocorrencia_periodicidade";                 let id = +params['id'];         this.ocorrencia_periodicidade = new Ocorrencia_periodicidade();         this.ocorrencia_periodicidade.cd_ocorrencia_periodicidade = id;         o.objetoJson = this.ocorrencia_periodicidade;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a ocorrencia_periodicidade. '; }      this.ocorrencia_periodicidade = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


